@import url(https://fonts.googleapis.com/css?family=Spartan:600&display=swap);
*{margin:0;box-sizing:border-box}body,.btn2{font-family:'Spartan',Cambria, Cochin, Georgia, Times, 'Times New Roman', serif}body{color:#6fffe9;text-align:center;background-color:#1c2541;margin-bottom:50px}a,a:active,a:visited,a:link{color:#4dbeff}a:hover{color:#008cff}#jobsStats,#serversStatus,#playerFinder{min-width:50%}#header{background-color:#0b132b}#header a{background-color:#008cff;padding:2px 5px;text-decoration:none;color:#000;border-radius:2px;box-shadow:black 1px 1px}h2,h3,#form{margin:5px;padding:5px;box-shadow:#0b132b 0 0 10px;background-color:#0b132b;border-radius:5px}h2,h3,#form{margin-left:0;margin-right:0;width:100%}table{background-color:#0b132b;width:100%}#jobsStats td,#carsStats td{padding:5px 50px}#split{height:10px}.btn2{border:0;background-color:#6fffe9;color:#0b132b;padding:5px;margin:0}.dxp{min-width:100px;cursor:help}.dxppointer{cursor:help}#header ul{display:flex;list-style:none;justify-content:center;padding:0}#header li{padding:5px 5px}#content{display:flex;align-content:center;justify-content:center}td{padding:3px}table{margin:5px 0;border-collapse:separate;border:1px solid #0b132b;border-radius:5px;align-content:center;justify-content:center}tr{border-collapse:separate;border:1px solid red}td{border-collapse:separate;border:1px solid rgba(32,121,255,0.205)}th{background-color:rgba(32,121,255,0.205);border-top-left-radius:5px;border-top-right-radius:5px}#content p{padding:10px}#form form *{padding:5px 0px}#playerFinder #form div *{width:100%}#playerFinder form input,select{text-align:center;color:white;background-color:#3a506b;border-radius:2px;border:0}#playerFinder form input{width:100%;border:0;text-align:center;background-color:#3a506b}#playerFinder form input[type="button"]{margin-top:10px;background-color:#008cff;color:#000;font-weight:bold}.online{color:white}.offline{color:#6fffe9}.loading{color:grey}#footer{position:fixed;bottom:0;background-color:#0b132b;width:100%}#footer p{max-width:600px;background-color:red;display:flex;justify-content:center;align-content:center}.no-avatar{height:50px;width:50px;margin:auto;background:url("/no-avatar.gif") no-repeat center;background-size:50px 50px}.avatar{border-radius:50%;background-color:#222222}.gold,.gold *{background-color:#5bc0be !important}.dark,.dark *{background-color:#3a506b !important}@media (max-width: 660px){#header ul{flex-direction:column;color:red}#header a{display:block}#header li{padding:1px 0}h2,h3{width:100%}#jobsStats td,#carsStats td,td,tr{padding:0;margin:0}body{margin-bottom:100px}td,b{font-size:.8em}#playerFinder .avatar,#playerFinder .no-avatar{display:none}#split{height:10px}.dxp{min-width:0}}

